import { useEffect, useState } from "react"
import { useNavigate, useParams } from "react-router-dom"
import { getCategoryApi, updateCategoryApi } from "../../component/service/category.service"
import { useSelector } from "react-redux"

function UpdateCategory(){
    let {id} = useParams()  //lay id tren duong dan
    console.log(id)

    let [category, setcategory] = useState({id, name: ""})  //doi tuong cate mac dinh la khoang trang

    let {
        categories
    } = useSelector((store) => store.category)
    
    let Navigate = useNavigate()

    let handleChangeName = (e) => {
        setcategory({ ...category, name: e.target.value})
    }

    let getData = async() => {
        //let body = await getCategoryApi(id)
        const body = categories.find(item => item.id === parseInt(id))
        setcategory(body) 
    }
    useEffect(() => {getData()}, [id])

    let handleSubmit = async() => {

        try {
            await updateCategoryApi(category)

            Navigate("/categories") //chuyen ve trang listcategories
        } catch (error) {
            console.log("Error Adding category ", error)
        }
        
    }
    return(
        <div>
            <label>Id:</label>
            <input name="id" value={id} readOnly/>  {/*chi doc */}

            <label>Name:</label>
            <input name="name" onChange={handleChangeName} value={category.name}/>

            <button type="button" onClick={handleSubmit}> Save</button>
        </div>
    )
}

export default UpdateCategory