import { createSlice } from "@reduxjs/toolkit";
import { searchCategoryApi } from "../../component/service/category.service";

export const categorySlide = createSlice({
    name: 'category',
    initialState: {
        categories: [],
        recordsFiltered: 0,
        totalPage: 0,
        "start": 0,
        "length": 5,
        "search": {
            "value": ""
        }
    },
    reducers: { //ham cap nhat gia tri trong initialState
        setCategories: (state, action) => {
            state.categories = action.payload
            //return action.payload;  //khong nen return neu payload khong trung thuoc tinh voiws initialState
        },
        setRecordsFiltered: (state, action) => {
            state.recordsFiltered = action.payload
        },
        setTotalPage: (state, action) => {
            state.totalPage = action.payload
        },
        setPage: (state, action) => {
            const {recordsFiltered, totalPage} = action.payload
            state.recordsFiltered = recordsFiltered
            state.totalPage = totalPage
        }
    }
})

// middleware thunk
export const searchThunk = (searchDTO) => {

    return async (dispath, getState) => {
        console.log(getState());    //tra ve du lieu thang counter
        //asyns
        
        try {
            let body = await searchCategoryApi(searchDTO)

            console.log(body.recordsFiltered)
            console.log(body.data);

            let totalPage = Math.ceil(body.recordsFiltered / searchDTO.length)

            // dispath(setRecordsFiltered(body.recordsFiltered))
            // dispath(setTotalPage(totalPage))
            dispath(setPage({totalPage, recordsFiltered: body.recordsFiltered}))

            dispath(setCategories(body.data))
            
        } catch (error) {
            console.log(error)
        }

    }
}

export const {setCategories, setRecordsFiltered, setTotalPage, setPage} = categorySlide.actions;
export default categorySlide.reducer