import { useContext, useEffect, useState } from "react"
import { Link, useNavigate } from "react-router-dom"
import { ThemeContext } from "../../context/ThemeContext";
import { deletePostApi, searchPostApi } from "../../component/service/post.service";

function Listpost() {
    let Navigate = useNavigate()

    //let ctx = useContext(ThemeContext)
    let {mode, setMode} = useContext(ThemeContext)

    let [posts, setPosts] = useState([])

    let [totalPage, setTotalPage] = useState("0")

    let [search, setSearch] = useState({
        "start": 0,
        "length": 10,
        "search": {
            "value": "%%"
        }
    })

    useEffect(() => {
        fetchData()
    }, [search])

    const fetchData = async () => {

        try {
            let body = await searchPostApi(search)

            console.log(body)
            setPosts(body.data)

            //tinh so trang
            totalPage = Math.ceil(body.recordsFiltered / search.length)
            setTotalPage(totalPage)
        } catch (error) {
            console.log(error)
        }

    }

    const handleSearch = (e) => {
        setSearch({
            ...search,
            "search": {
                "value": e.target.value
            }
        }
        )
    }
    const handleNext = () => {
        setSearch({
            ...search,
            start: search.start + search.length
        })
    }
    const handlePrev = () => {
        setSearch({
            ...search,
            start: search.start > 0 ? search.start - search.length : 0
        })
    }
    const handlePage = (index) => {
        setSearch({
            ...search,
            start: index * search.length
        })
    }

    const handleChangeSize = (e) => {
        const size = e.target.value
        setSearch({
            ...search,
            length: parseInt(size)
        })
    }

    const handleDelete = async (id) => {
        try {
            await deletePostApi(id)
            fetchData()
        } catch (error) {
            console.log(error)
        }
    }


    return (
        <div>
            <Link to={"/post/create"}> Create Post</Link>

            <div>
                <input onChange={handleSearch} />
            </div>

            <table border={1}>
                <thead>
                    <tr>
                        <th>Id</th>
                        <th>Title</th>
                        <th>Description</th>
                        <th>Image</th>
                        <th>Category</th>
                        <th>Create At</th>
                        <th>Create By</th>
                        <th>Actions</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        posts.map(c => {
                            return (
                                <tr key={c.id}>
                                    <td>{c.id}</td>
                                    <td>{c.title}</td>
                                    <td>{c.description}</td>
                                    <td>
                                        {c.images?.map(image => (
                                            <img src={`http://45.117.177.43:8080/image/${image}`}  alt="" width={100} key={image}/>
                                        ))}
                                        
                                    </td>
                                    <td>{c.categoryName}</td>
                                    <td>{c.createdDate}</td>
                                    <td>{c.createBy}</td>
                                    <td>
                                        <button type="button" onClick={()=> Navigate(`/post/update/${c.id}`)}>Edit</button> |
                                        <button type="button"  onClick={() => handleDelete(c.id)} >Delete</button>
                                    </td>
                                </tr>
                            )

                        })
                    }
                </tbody>
            </table>
            <select onChange={handleChangeSize}>
                <option value={5}>5</option>
                <option value={10}>10</option>
                <option value={20}>20</option>
            </select>
            <button onClick={handlePrev}> Prev </button>
            {
                Array.apply(null, { length: totalPage }).map((_, index) => {
                    return (
                        <button key={index} onClick={() => handlePage(index)}> {index} </button>
                    )
                })
            }
            <button onClick={handleNext}> Next </button>

            <p> Tong so trang: {totalPage} </p>
        </div>

    )
}
export default Listpost