import { Avatar, Stack } from "@mui/material";
import Login from "../component/auth/Login";
import Footer from "../component/shared/Footer";

function LoginPage(){
    return(
        <Stack spacing={2} 
                direction="column" 
                justifyContent="center" 
                alignItems="center" 
                sx={{ minHeight: '100vh', transform: 'translateY(-10%)' }}>
            <Avatar
                alt="User Avatar"
                src="https://images.baodantoc.vn/uploads/2022/Th%C3%A1ng%208/Ng%C3%A0y_31/Nga/quockyvietnam-copy-7814.jpg" // Thay đường dẫn bằng hình ảnh của bạn
                sx={{ width: 100, height: 100 }}
            />
            <Login/>
            <Footer/>
        </Stack>
    )
}

export default LoginPage;