import { axiosInstan } from "../util/axios";

export let AddPostApi = (data) => {

    let config = {
        method: 'post',
        maxBodyLength: Infinity,
        url: '/member/post/add',
        headers: {
            'Authorization': 'Bearer ' + localStorage.getItem("token"),
            'Cookie': 'JSESSIONID=E8207D3A48693D13F2A07C0975CA792E'
        },
        data: data
    };

    return axiosInstan(config)
        .then((response) => {
            console.log(JSON.stringify(response.data));

            return response.data
        })
        .catch((error) => {
            console.log(error);
        });
}
