export function searchPostApi(search) {
    let data = JSON.stringify(search);
    
    let config = {
        method: 'post',
        maxBodyLength: Infinity,
        url: '/post/search',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + localStorage.getItem("token"),
            //'Cookie': 'JSESSIONID=E39E0E5FBB5F45FA78866D0EA338F072'
        },
        data: data
    };

    return axiosInstan(config)
        .then((response) => {
            console.log(JSON.stringify(response.data));
            console.log("huhu")
            console.log(response.data)
            return response.data  //du lieu tra ve
        })
        .catch((error) => {
            console.log(error);
            throw error;
        });
}

export const getPostApi = (id) =>{
    let data = '';

    let config = {
    method: 'get',
    maxBodyLength: Infinity,
    url: '/post/' + id,
    headers: { 
        'Authorization': 'Bearer ' + localStorage.getItem("token"), 
        'Cookie': 'JSESSIONID=052AB4748A2419E39944D9AE1163E363'
    },
    data : data
    };

    return axiosInstan(config)
    .then((response) => {
        console.log(JSON.stringify(response.data));
        return response.data
    })
    .catch((error) => {
        console.log(error);
    });
}

export const deletePostApi = (id) => {
    
    let config = {
        method: 'delete',
        maxBodyLength: Infinity,
        url: '/admin/post/delete?id=' + id,
        headers: { 
        'Authorization': 'Bearer ' + localStorage.getItem("token")
        }
    };
  
    return axiosInstan(config)
    .then((response) => {
        console.log(JSON.stringify(response.data));
        return response.data
    })
    .catch((error) => {
        console.log(error);
    });
}
