import { useState } from "react"
import { AddCategoryApi } from "../../component/service/category.service"
import { useNavigate } from "react-router-dom"
import Button from '@mui/material/Button';
import { toast } from "react-toastify";


function AddCategory(){

    let [category, setcategory] = useState({name: ""})  //doi tuong cate mac dinh la khoang trang

    let Navigate = useNavigate()

    let handleChangeName = (e) => {
        setcategory({ ...category, name: e.target.value})
    }

    let handleSubmit = async() => {

        try {
            await AddCategoryApi(category)

            toast("Them thanh cong danh muc!")

            Navigate("/categories") //chuyen ve trang listcategories
        } catch (error) {
            console.log("Error Adding category ", error)
        }
        
    }
    return(
        <div>

            <label>Name:</label>
            <input name="name" onChange={handleChangeName}/>

            {/* <button type="button" onClick={handleSubmit}> Save</button> */}
            <Button variant="contained" onClick={handleSubmit}>Save</Button>
        </div>
    )
}

export default AddCategory