import { createSlice } from "@reduxjs/toolkit";

export const counterSlide = createSlice({
    name: 'counter',
    initialState: {
        x: 0
    },
    reducers: { //ham cap nhat gia tri trong initialState
        increment: (state) => {
            state.x += 1;
        },
        decrement: (state) => {
            state.x -= 1;
        },
        incNumber: (state, action) => {
            console.log(action.type);
            state.x += action.payload;
        }
    }
})

//thunk
export const increatementAsyns = (amount) => {

    return (dispath, getState) => {
        console.log(getState());    //tra ve du lieu thang counter
        //asyns

        setTimeout(() => {
            dispath(incNumber(amount))
        }, 1000)
    }
}

export const {increment, decrement, incNumber} = counterSlide.actions;
console.log(increment);
console.log(decrement);
console.log(incNumber);
export default counterSlide.reducer