import axios from "axios";
import nProgress from "nprogress";

export const axiosInstan = axios.create({
    baseURL: 'http://45.117.177.43:8080/api'
})

axiosInstan.interceptors.request.use(function(config){
    nProgress.start();
    return config;
}, function (error){
    return Promise.reject(error);
});

axiosInstan.interceptors.response.use(function(resp){
    nProgress.done();
    return resp;
}, function (error){
    return Promise.reject(error);
});