import { axiosInstan } from "../util/axios";

export let AddCategoryApi = (category) => {

    let data = JSON.stringify(category);

    let config = {
        method: 'post',
        maxBodyLength: Infinity,
        url: '/admin/category/add',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + localStorage.getItem("token"),
            'Cookie': 'JSESSIONID=E8207D3A48693D13F2A07C0975CA792E'
        },
        data: data
    };

    return axiosInstan(config)
        .then((response) => {
            console.log(JSON.stringify(response.data));

            return response.data
        })
        .catch((error) => {
            console.log(error);
        });
}

export const updateCategoryApi = (category) => {
    let data = JSON.stringify({
        "id": category.id,
        "name": category.name
      });
      
    let config = {
        method: 'put',
        maxBodyLength: Infinity,
        url: '/admin/category/update',
        headers: { 
          'Content-Type': 'application/json', 
          'Authorization': 'Bearer ' + localStorage.getItem("token"), 
          'Cookie': 'JSESSIONID=052AB4748A2419E39944D9AE1163E363'
        },
        data : data
    };
      
      return axiosInstan(config)
      .then((response) => {
        console.log(JSON.stringify(response.data));
        return response.data
      })
      .catch((error) => {
        console.log(error);
      });
      
}

export const deleteCategoryApi = (id) => {
    
    let config = {
        method: 'delete',
        maxBodyLength: Infinity,
        url: '/admin/category/delete?id=' + id,
        headers: { 
        'Authorization': 'Bearer ' + localStorage.getItem("token")
    }
  };
  
  return axiosInstan(config)
  .then((response) => {
    console.log(JSON.stringify(response.data));
    return response.data
  })
  .catch((error) => {
    console.log(error);
  });
}
export function searchCategoryApi(search) {
    let data = JSON.stringify(search);
    
    let config = {
        method: 'post',
        maxBodyLength: Infinity,
        url: '/category/search',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + localStorage.getItem("token"),
            //'Cookie': 'JSESSIONID=E39E0E5FBB5F45FA78866D0EA338F072'
        },
        data: data
    };

    return axiosInstan(config)
        .then((response) => {
            console.log(JSON.stringify(response.data));
            console.log("huhu")
            console.log(response.data)
            return response.data  //du lieu tra ve
        })
        .catch((error) => {
            console.log(error);
            throw error;
        });
}

export const getCategoryApi = (id) =>{
    let data = '';

    let config = {
    method: 'get',
    maxBodyLength: Infinity,
    url: '/category/' + id,
    headers: { 
        'Authorization': 'Bearer ' + localStorage.getItem("token")
    },
    data : data
    };

    return axiosInstan(config)
    .then((response) => {
        console.log(JSON.stringify(response.data));
        return response.data
    })
    .catch((error) => {
        console.log(error);
        throw error
    });
}
