import { useEffect, useState } from "react"
import { useNavigate } from "react-router-dom"
import { AddPostApi } from "../../component/service/post.service"
import { searchCategoryApi } from "../../component/service/category.service"
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import Button from '@mui/material/Button';

function AddPost() {
    //lay category
    let [categories, setCategories] = useState([])
    let [search, setSearch] = useState({
        "start": 0,
        "length": 10,
        "search": {
            "value": ""
        }
    })
    useEffect(() => {
        fetchData()
    }, [search])

    const fetchData = async () => {

        try {
            let body = await searchCategoryApi(search)

            console.log(body)
            setCategories(body.data)

        } catch (error) {
            console.log(error)
        }

    }

    let [post, setPost] = useState({ 
        title: "", 
        description: "", 
        categoryId: '', 
        imageFile: [] })  //doi tuong cate mac dinh la khoang trang

    let Navigate = useNavigate()

    let handleChangeText = (e) => {
        setPost({ ...post, [e.target.name]: e.target.value })
    }

    let handleChangeFile = (e) => {
        //setPost({ ...post, imageFile: e.target.files[0]})
        setPost({ ...post, imageFile: Array.from(e.target.files)})
    }

    let handleSubmit = async () => {

        try {
            let data = new FormData();
            data.append('title', post.title);
            data.append('description', post.description);
            data.append('categoryId', post.categoryId);
            //data.append('imageFile', post.imageFile)
            post.imageFile.forEach((file, index) => {
                data.append(`imageFile[${index}]`, file)
            })
            await AddPostApi(data)

            Navigate("/posts") //chuyen ve trang listcategories
        } catch (error) {
            console.log("Error Adding post ", error)
        }

    }
    return (
        <div>
            <div>
                <label>Title: </label>
                <input name="title" onChange={handleChangeText} />
            </div>
            <div>
                <label>Description: </label>
                <ReactQuill style={{height: 150}} theme="snow" value={post.description} onChange={(text) => setPost({...post, description: text})} />
            </div>
            <div style={{marginTop: 50}}>
                <label>CategoryID: </label>
                <select name="categoryId" onChange={handleChangeText} >
                    {categories.map( c => {
                        return <option key={c.id} value={c.id}>{c.name}</option>
                    })}
                </select>
            </div>
            <div>
                <label>Image File: </label>
                <input type="file" accept="image/*" multiple onChange={handleChangeFile} /><br/>
            </div>
            {/* <button type="button" onClick={handleSubmit}> Save</button> */}
            <div>
                <Button variant="contained" onClick={handleSubmit}>Save</Button>
            </div>
        </div>
    )
}

export default AddPost