import { useContext } from "react";
import { Link } from "react-router-dom";
import { ThemeContext } from "../../context/ThemeContext";
import { AuthContext } from "../../context/AuthContext";
import Button from '@mui/material/Button';
import { useMe } from "../hook/useAuth";
import { useDispatch, useSelector } from "react-redux";
import { store } from "../redux/store";
import { incNumber, increatementAsyns, increment } from "../redux/couterSlide";

function Menu() {
    let {mode, setMode} = useContext(ThemeContext)
    let {setAuth, user} = useContext(AuthContext)

    //co the dung hook
    const u = useMe()

    const counter = useSelector((store) => store.counter)
    console.log(counter)
    const dispatch = useDispatch();

    let handleMode = () => {
        setMode(mode === 'light' ? 'dark' : 'light')
        //dispatch(increment());  //goi actions type increatement
        //dispatch(incNumber(10))
        dispatch(increatementAsyns(10))
    }
    let handleLogout = () =>{
        setAuth(false)
        localStorage.removeItem("token")
    }

    return (
        <ul>
            <li>Xin chao {u.name} - {counter.x} </li>
            <li>
                <button onClick={handleMode}>{mode}</button>
            </li>
            
            <li>
                <Link to='/'> Home</Link>
            </li>

            <li>
                <Link to='/test'>Test</Link>
            </li>

            <li>
                <Link to='/categories'>Category</Link>
            </li>

            <li>
                <Link to='/posts'>Posts</Link>
            </li>

            <li>
                <Link to='/user'>User</Link>
            </li>

            <li>
                <Button variant="outlined" color="error" onClick={handleLogout} >Logout</Button>
            </li>
        </ul>
    )
}

export default Menu;