import { useEffect, useState } from "react"
import { useNavigate, useParams } from "react-router-dom"
import { UpdatePostApi, getPostApi } from "../../component/service/post.service"
import { searchCategoryApi } from "../../component/service/category.service"
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';


function UpdatePost() {
    //lay category
    let [categories, setCategories] = useState([])
    let [search, setSearch] = useState({
        "start": 0,
        "length": 10,
        "search": {
            "value": ""
        }
    })
    useEffect(() => {
        fetchData()
    }, [search])

    const fetchData = async () => {

        try {
            let body = await searchCategoryApi(search)

            console.log(body)
            setCategories(body.data)

        } catch (error) {
            console.log(error)
        }

    }


    let { id } = useParams()  //lay id tren duong dan
    console.log(id)

    let [post, setPost] = useState({
        id,
        title: "",
        description: "",
        categoryId: '',
        imageFile: []
    })  //doi tuong cate mac dinh la khoang trang


    let Navigate = useNavigate()


    let handleChangeText = (e) => {
        setPost({ ...post, [e.target.name]: e.target.value })
    }

    let handleChangeFile = (e) => {
        //setPost({ ...post, imageFile: e.target.files[0]})
        setPost({ ...post, imageFile: Array.from(e.target.files) })
    }

    let getData = async () => {
        let body = await getPostApi(id)
        setPost(body)
    }
    useEffect(() => { getData() }, [id])


    let handleSubmit = async () => {

        try {
            let data = new FormData();
            data.append('id', post.id)
            data.append('title', post.title);
            data.append('description', post.description);
            data.append('categoryId', post.categoryId);
            //data.append('imageFile', post.imageFile)
            post.imageFile.forEach((file, index) => {
                data.append(`imageFile[${index}]`, file)
            })
            await UpdatePostApi(data)

            Navigate("/posts") //chuyen ve trang listcategories
        } catch (error) {
            console.log("Error Adding post ", error)
        }

    }
    return (
        <div>
            <div>
                <label>Id:</label>
                <input name="id" value={id} readOnly />  {/*chi doc */}
            </div>
            <div>
                <label>Title: </label>
                <input name="title" onChange={handleChangeText} value={post.title} />
            </div>
            <div>
                <label>Description: </label>
                <ReactQuill style={{ height: 150 }} theme="snow" defaultValue={post.description} onChange={(text) => setPost({ ...post, description: text })} />
            </div>
            <div style={{ marginTop: 50 }}>
                <label>CategoryID: </label>
                <select name="categoryId" onChange={handleChangeText} >
                    {categories.map(c => {
                        return <option key={c.id} value={c.id}>{c.name}</option>
                    })}
                </select>
            </div>
            <div>
                <label>Image File: </label>
                <input type="file" accept="image/*" multiple onChange={handleChangeFile} />
            </div>

            <button type="button" onClick={handleSubmit}> Save</button>
        </div>
    )
}

export default UpdatePost