import { useContext, useEffect, useState } from "react"
import { Link, useNavigate } from "react-router-dom"
import { deleteCategoryApi, searchCategoryApi } from "../../component/service/category.service";
import { ThemeContext } from "../../context/ThemeContext";
import { Pagination, Stack } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { searchThunk } from "../../component/redux/categorySlide";
import { toast } from "react-toastify";
import { type } from "@testing-library/user-event/dist/type";

function Listcategory() {
    let Navigate = useNavigate()
    const dispatch = useDispatch();

    let {
        categories,
        recordsFiltered,
        totalPage
    } = useSelector((store) => store.category)

    //let ctx = useContext(ThemeContext)
    let {mode, setMode} = useContext(ThemeContext)

    //let [categories, setCategories] = useState([])

    //let [totalPage, setTotalPage] = useState("0")

    let [search, setSearch] = useState({
        "start": 0,
        "length": 5,
        "search": {
            "value": ""
        }
    })

    useEffect(() => {
        fetchData()
    }, [search])

    const fetchData = async () => {
        dispatch(searchThunk(search))

    }

    const handleSearch = (e) => {
        setSearch({
            ...search,
            "search": {
                "value": e.target.value
            }
        }
        )
    }
    const handleNext = () => {
        setSearch({
            ...search,
            start: search.start + search.length
        })
    }
    const handlePrev = () => {
        setSearch({
            ...search,
            start: search.start > 0 ? search.start - search.length : 0
        })
    }
    const handlePage = (index) => {
        setSearch({
            ...search,
            start: index * search.length
        })
    }

    const handleChangeSize = (e) => {
        const size = e.target.value
        setSearch({
            ...search,
            length: parseInt(size)
        })
    }

    const handleDelete = async (id) => {
        try {
            await deleteCategoryApi(id)
            fetchData()
            toast("Xoa thanh cong danh muc!", {
                type: 'success'
            })
        } catch (error) {
            console.log(error)
            toast("Xoa that bai danh muc!", {
                type: 'error'
            })
        }
    }


    return (
        <div>
            <Link to={"/category/create"}> Create Category</Link>

            <div>
                <input onChange={handleSearch} />
            </div>

            <table border={1}>
                <thead>
                    <tr>
                        <th>Id</th>
                        <th>Name</th>
                        <th>Actions</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        categories.map(c => {
                            return (
                                <tr key={c.id}>
                                    <td>{c.id}</td>
                                    <td>{c.name}</td>
                                    <td>
                                        <button type="button" onClick={()=> Navigate(`/category/update/${c.id}`)}>Edit</button> |
                                        <button type="button"  onClick={() => handleDelete(c.id)} >Delete</button>
                                    </td>
                                </tr>
                            )

                        })
                    }
                </tbody>
            </table>
            <Stack spacing={2} direction="row" alignItems="center">
                <select onChange={handleChangeSize}>
                    <option value={5}>5</option>
                    <option value={10}>10</option>
                    <option value={20}>20</option>
                </select>
                {/* <button onClick={handlePrev}> Prev </button>
                {
                    Array.apply(null, { length: totalPage }).map((_, index) => {
                        return (
                            <button key={index} onClick={() => handlePage(index)}> {index} </button>
                        )
                    })
                }
                <button onClick={handleNext}> Next </button> */}
                <Pagination count={totalPage} variant="outlined" color="primary"  showFirstButton showLastButton onChange={(e, value) => handlePage(value-1)}/>
            {/* <p> Tong so trang: {totalPage} </p> */}
            </Stack>
        </div>

    )
}
export default Listcategory