
import { useEffect, useState } from 'react';

function TestPage() {
  let [personList, setPersonList] = useState([{ id: 1, name: "A1" }])
  let [editPerson, setEditPerson] = useState(null)
  return (
    <div>
      <h1>hiu hiu</h1>

      <Hello name="TXT" />
      <Form editPerson={editPerson} personArray={personList} setPersonArray={setPersonList} />
      <Table setEditPerson={setEditPerson} personArray={personList} setPersonArray={setPersonList} />

    </div>
  );
}


//function component
function Hello(prop) {
  let name = prop.name
  return (
    <div>
      <h1> hehe {prop.name}</h1>
    </div>
  )
}

function Form({ editPerson, personArray, setPersonArray }) {
  //state
  let [id, setId] = useState("")
  let [name, setName] = useState("")

  useEffect(() => {
    setId(editPerson?.id || "")
    setName(editPerson?.name || "")
  }, [editPerson])

  let onChangeHandler = (e) => {
    console.log(e.target.name)
    console.log(e.target.value)

    if (e.target.name === 'id') {
      setId(e.target.value)
    }
    if (e.target.name === 'name') {
      setName(e.target.value)
    }

  }

  let submitEditForm = (e) => {
    console.log("update")
    let person = { id, name }
    console.log(person)

    //update
    if (editPerson) {
      setPersonArray(currentVar => {
        //tim vi tri editperson
        const index = currentVar.findIndex(p => p.id === editPerson.id)
        //thay the doi tuong person
        let newArr = [...currentVar]
        newArr[index] = person
        return newArr
      })
      return;
    }

  }
  let submitCreateForm = (e) => {
    console.log("SAVE")
    let person = { id, name }
    console.log(person)

    //set state cua app component
    //c1
    //let newArr = [...personArray, person] //them phan tu vao mang cu
    //setPersonArray(newArr)
    //c2
    // let idCheck = personArray.some(p => p.id === id)
    // if (idCheck) {
    //   return
    // }
    //add
    //setPersonArray(currentVar => ([...currentVar, person]))

    //add
    setPersonArray(currentVar => ([...currentVar, person]))
  }

  return (
    <div>
      <label style={{ marginRight: '5px' }} >Id: </label>
      <input name='id' style={{ marginRight: '10px' }} onChange={onChangeHandler} value={id}></input>

      <label style={{ marginRight: '5px' }}>Name:</label>
      <input name='name'
        style={{ marginRight: '10px' }}
        className={name.length === 0 ? 'inputDefaut' : (name.length < 6 ? 'inputError' : 'inputSuccess')}
        onChange={onChangeHandler}
        value={name}></input>

      <button type='button' onClick={submitCreateForm}>Create</button>
      <button type='button' onClick={submitEditForm}>Edit</button>
      <div>
        {id} - {name}
      </div>
    </div>
  );
}

function Table({ setEditPerson, personArray, setPersonArray }) {
  // let personArray = prop.personArray
  //const {personArray} = prop   -----object destruction
  const deleteRow = (id) => {
    // xoa phan tu co id trung
    let newArr = personArray.filter(p => p.id !== id)
    setPersonArray(newArr)
  }

  return (
    <table border={1}>
      <thead>
        <tr>
          <th> Id </th>
          <th> Name </th>
          <th> Action </th>
        </tr>
      </thead>
      <tbody>
        {
          personArray.map(p => {
            return (
              <tr key={p.id}>
                <td>{p.id}</td>
                <td>{p.name}</td>
                <td>
                  <button type='button' onClick={() => deleteRow(p.id)}> Delete </button>

                  <button type='button' onClick={() => setEditPerson(p)}> Edit </button>
                </td>
              </tr>
            )
          })
        }
      </tbody>
    </table>
  )
}

export default TestPage;
